<template>
  <li v-if="value || editMode && editable" class="detail">
    <span class="label">{{ label }}:</span>
    <template v-if="editMode && editable">
      <yes-no
        class="yes-no"
        v-if="binary"
        v-model="modelValue"
        :options="{0: $t('NO'), 1: $t('YES')}"
      />
      <editable-line v-else v-model="modelValue" />
    </template>
    <slot v-else>{{ value }}</slot>
  </li>
</template>

<script>
import detailsViewMixin from '@/mixins/detailsView.js';
import EditableLine from './EditableLine.vue';
import YesNo from '../../../components/YesNo.vue';

export default {
  mixins: [detailsViewMixin],
  components: { EditableLine, YesNo },
  props: {
    label: String,
    value: [String, Number],
    editable: Boolean,
    binary: Boolean,
  },
  computed: {
    modelValue: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit('input', newValue);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.detail {
  margin-bottom: 5px;
  line-height: 20px;
}

.label {
  color: #525d66;
  margin-right: 15px;
}

.yes-no {
  display: inline-flex;
}
</style>
