<template>
    <textarea
        v-if="editMode"
        v-model="modelValue"
        class="textarea"
        rows="9"
    />
    <p v-else class="text">{{ value }}</p>
</template>

<script>
import detailsViewMixin from '@/mixins/detailsView.js';

export default {
  mixins: [detailsViewMixin],
  props: {
    value: String,
  },
  computed: {
    modelValue: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit('input', newValue);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.textarea {
    background: none;
    border: 1px solid currentColor;
    color: currentColor;
    font-size: inherit;
    font-weight: inherit;
    border-radius: 0.1rem;
    padding: 0.3rem 0.5rem;
    width: 100%;
    resize: vertical;
}

.text {
  white-space: pre-line;
}
</style>
