<template>
    <input v-if="editMode" v-model="modelValue" class="input"/>
    <span v-else>
        {{ value }}
    </span>
</template>

<script>
import detailsViewMixin from '@/mixins/detailsView.js';

export default {
  mixins: [detailsViewMixin],
  props: {
    value: String,
  },
  computed: {
    modelValue: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit('input', newValue);
      },
    },
  },
};
</script>

<style lang="scss" scoped>
.input {
    background: none;
    border: 1px solid currentColor;
    color: currentColor;
    font-size: inherit;
    font-weight: inherit;
    border-radius: 0.1rem;
    padding: 0.3rem 0.5rem;
}
</style>
