            <div ref="desc__inner">
              <editable-markdown class="heading__text" v-model="ug.object.description" />
            </div>
            <a href="#" class="more" @click.prevent="isDescExpanded = true">{{
              $t("VIEW MORE")
            }}</a>
          </div>
        </div>
        <div class="section" v-if="areScreenshotsVisible || editMode">
          <h2 class="h2">
            {{ $t("SCREENSHOTS") }}
            <ul class="select">
              <li
