          >
            {{ $t('ADD_SCREENSHOTS') }}
          </v-button>

          <ul
            class="screenshots"
            v-for="(array, os) in ug.object.screenshots_uris_by_os_type"
            :key="os"
            v-show="screenshotsOS === os && array.length"
          >
            <li
              class="screenshots__item"
              v-for="(screenshot, idx) in array"
              :key="screenshot"
            >
              <img class="screenshots__img" :src="screenshot" />

              <button
                v-if="editMode"
                class="screenshots__remove-button"
                @click="removeScreenshot(os, idx)"
              >
                <img src="@/assets/icon-x.svg" />
              </button>
            </li>
          </ul>
        </div>
      </div>
      <!-- TODO sidebar -->
      <div class="main__content main__content_b main__content_ptop">
        <div v-if="ug.object.whatsnew || editMode" class="section">
          <h2 class="h2">{{ $t("WHATSNEW") }}</h2>
          <div
            v-if="ug.object.whatsnew || editMode"
            ref="changelog"
            class="changelog"
            :class="{ changelog_expanded: isChangelogExpanded || editMode }"
          >
            <editable-markdown ref="changelog__inner" v-model="ug.object.whatsnew" />
            <a
              href="#"
              class="more"
              @click.prevent="isChangelogExpanded = true"
              >{{ $t("VIEW MORE") }}</a
            >
          </div>
        </div>
        <div class="section section_noborder">
          <h2 class="h2 h2_badge">
            {{ $t("INFORMATION") }}
          </h2>
          <div class="info">
            <ul class="info__list">
              <v-detail v-if="editMode" :label="$t('PUBLIC')" v-model="ug.object.is_public" editable binary />
              <v-detail v-if="editMode" :label="$t('USE_SIDELOADING_PATCH')" v-model="ug.object.use_sideloading_patch" editable binary />
              <v-detail v-if="editMode" :label="$t('USE_EXPLICIT_BUNDLE_ID')" v-model="ug.object.use_explicit_bundle_id" editable binary />
              <v-detail v-if="editMode" :label="$t('USE_GROUP_PATCH')" v-model="ug.object.use_group_patch" editable binary />

              <v-detail v-if="editMode" label="18+" v-model="ug.object.is_18plus" editable binary />

              <v-detail v-if="editMode" :label="$t('SHORT_DESCRIPTION')" v-model="ug.object.lead" editable />

              <v-detail :label="$t('DEVELOPER')" v-model="ug.object.developer_name" editable />
              <v-detail :label="$t('WEBSITE')" v-model="ug.object.website_uri" editable>
                <a class="info__link" :href="ug.object.website_uri" target="_blank">
                  {{ $t("VISIT") }}
                </a>
              </v-detail>

              <!-- Q is it in dollars? what if it's free? -->
              <v-detail :label="$t('PRICE')" :value="ug.object.price_cents">
                {{ (ug.object.price_cents / 100).toFixed(2) }}
              </v-detail>

              <v-detail :label="$t('CATEGORY')" v-model="ug.object.genre_name" editable />
              <v-detail :label="$t('UPDATED')" :value="ug.object.updated_at">
                {{ new Date(ug.object.updated_at).toLocaleDateString() }}
              </v-detail>
              <v-detail :label="$t('SIZE')" :value="ug.object.size_hr" />

              <v-detail :label="$t('OS_VERSION', {os: 'iOS'})" v-model="ug.object.min_ios_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', {os: `iOS ${ug.object.min_ios_version}`}) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'iPadOS'})" v-model="ug.object.min_ipados_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', {os: `iPadOS ${ug.object.min_ipados_version}`}) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'tvOS'})" v-model="ug.object.min_tvos_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', {os: `tvOS ${ug.object.min_tvos_version}`}) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'macOS'})" v-model="ug.object.min_macos_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', {os: `macOS ${ug.object.min_macos_version}`}) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'watchOS'})" v-model="ug.object.min_watchos_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', {os: `watchOS ${ug.object.min_watchos_version}`}) }}
              </v-detail>
              <v-detail :label="$t('OS_VERSION', {os: 'visionOS'})" v-model="ug.object.min_visionos_version" editable>
                {{ $t('REQUIRES_MIN_OS_VERSION', {os: `visionOS ${ug.object.min_visionos_version}`}) }}
              </v-detail>

              <v-detail :label="$t('LICENSE')" v-model="ug.object.license" editable />
              <v-detail :label="$t('TOS')" v-model="ug.object.tos_uri" editable>
                <a class="info__link" :href="ug.object.tos_uri" target="_blank">
                  {{ $t("VIEW") }}
                </a>
              </v-detail>
              <v-detail :label="$t('PRIVACY_POLICY')" v-model="ug.object.privacy_policy_uri" editable>
                <a class="info__link" :href="ug.object.privacy_policy_uri" target="_blank">
                  {{ $t("VIEW") }}
                </a>
